@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,400;1,400;1,700&display=swap);
body {
  margin: 0;
  background-color: #1c1c1c;
}

* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
}

.header {
  box-sizing: border-box;
  max-width: 100vw;
  background-color: #292828;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em 3em;
  position: relative;
}

.logo {
  object-fit: center;
  max-height: 50px;
  cursor: pointer;
}

.fav-cart-container {
  display: flex;
  padding: 0;
  margin: 0;
  justify-content: space-around;
  grid-column-gap: 2em;
  -webkit-column-gap: 2em;
          column-gap: 2em;
}
.headerBtn {
  background-color: transparent;
  display: flex;
  border: none;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  font-size: 2.5em;
  outline: none;
  z-index: 1;
  margin: 0;
  transition: all 0.25s ease;
}

.favoritesBtn:hover {
  color: rgb(206, 23, 23);
  transform: scale(1.3);
}

.willBuyBtn:hover {
  color: rgb(30, 192, 30);
  transform: scale(1.3);
}

.grid-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin: 2em auto;
  align-items: start;
  grid-column-gap: 0.5em;
  -webkit-column-gap: 0.5em;
          column-gap: 0.5em;
  grid-row-gap: 3em;
  row-gap: 3em;
}

.loader {
  margin: 0 auto;
  text-align: center;
  margin-top: 15em;
}

.img-container {
  max-width: 300px;
  max-height: 250px;
  overflow: hidden;
}

.image {
  max-width: 300px;
  max-height: 250px;
  transition: all 0.4s ease;
  overflow: hidden;
  position: relative;
  cursor: pointer;
}

.image:hover {
  transform: scale(1.2);
  -webkit-filter: brightness(125%);
          filter: brightness(125%);
}

.content-container-default {
  margin: 0 auto;
  box-shadow: 0px 0px 4px 0px rgba(255, 255, 255, 0.5);
}

.content-container-willBuy-Favorite {
  margin: 0 auto;
  box-shadow: -3px -3px 10px 0px rgb(19, 216, 19),
    3px 3px 10px 0px rgb(236, 16, 16);
}

.content-container-willBuy {
  margin: 0 auto;
  box-shadow: -3px -3px 10px 0px rgb(19, 216, 19);
}

.content-container-Favorite {
  margin: 0 auto;
  box-shadow: 3px 3px 10px 0px rgb(236, 16, 16);
}

.button-container {
  display: flex;
  padding: 0.75em 0;
  margin: 0;
  justify-content: space-around;
}

.button-container button {
  background-color: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  font-size: 1.8em;
  outline: none;
}

.favoriteBtn:hover {
  color: rgb(206, 23, 23);
}

.cartBtn:hover {
  color: rgb(30, 192, 30);
}

.classForCheckedFav {
  color: rgb(206, 23, 23);
}

.classForCheckedCart {
  color: rgb(30, 192, 30);
}

.headerBtn i,
.button-container i,
.favoriteBtn i,
.cartBtn i {
  position: relative;
  z-index: -1;
}

[data-favorite-count] {
  position: relative;
}

[data-favorite-count]::before {
  content: attr(data-favorite-count);
  position: absolute;
  top: -2.7px;
  right: -2.7px;
  width: 15px;
  height: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: rgb(206, 23, 23);
  color: white;
  font-size: 9px;
}

[data-willbuy-count] {
  position: relative;
}

[data-willbuy-count]::before {
  content: attr(data-willbuy-count);
  position: absolute;
  top: -2.7px;
  right: -2.7px;
  width: 15px;
  height: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: rgb(30, 192, 30);
  color: white;
  font-size: 9px;
}

@media (max-width: 400px) {
  .header {
    flex-direction: column;
    align-items: center;
    grid-row-gap: 1em;
    row-gap: 1em;
    padding: 1em 0em;
  }
  .fav-cart-container {
    width: 100%;
    grid-column-gap: 0em;
    -webkit-column-gap: 0em;
            column-gap: 0em;
  }

  .img-container {
    max-width: 220px;
  }

  .image {
    max-width: 220px;
  }

  .grid-content {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 700px) {
  .loader {
    margin: 0 auto;
    text-align: center;
    margin-top: 10em;
  }
}

body {
  font-family: "Source Sans Pro", sans-serif;
  color: rgba(255, 255, 255, 0.7);
  padding-bottom: 4em;
}
.back-to-shopping {
  display: flex;
  text-decoration: none;
  align-items: center;
  color: #fff;
  font-weight: 700;
  transition: all 250ms ease;
}

.back-to-shopping:hover {
  transform: scale(1.2);
}

.check-out {
  margin: 0 auto;
  text-align: center;
  margin-top: 0.75em;
  font-size: 3em;
  letter-spacing: 3px;
  margin-bottom: 1em;
}

.all-items-container {
  display: flex;
  flex-direction: column;
  max-width: 800px;
  margin: 2em auto;
  margin-bottom: 0.5em;
  justify-content: center;
  width: 90%;
}

.item-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1em;
  padding-bottom: 1em;
  border-bottom: 1px solid rgba(255, 255, 255, 0.7);
}

.bin-and-image {
  display: flex;
  align-items: center;
  grid-column-gap: 1em;
  -webkit-column-gap: 1em;
          column-gap: 1em;
}

.buyImg {
  max-width: 240px;
  transition: all 0.4s ease;
  cursor: pointer;
}

.buyImg:hover {
  transform: scale(1.05);
  -webkit-filter: brightness(125%);
          filter: brightness(125%);
}

.btn-delete,
.btn-delete:focus {
  background-color: transparent;
  color: rgba(255, 255, 255, 0.7);
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  font-size: 1em;
  outline: none;
  position: relative;
}

.btn-delete:hover {
  color: rgba(255, 0, 0, 0.7);
  outline: none;
}

.btn-delete i {
  position: relative;
  z-index: -1;
}

.messages {
  max-width: 800px;
  margin: 0 auto;
  display: block;
  width: 90%;
}

.messages .total {
  text-align: end;
  margin-left: auto;
}

.messages .no-items-msg {
  text-align: start;
  font-style: italic;
  letter-spacing: 1.3px;
}

.order-btn {
  display: block;
  padding: 1em 2em;
  font-size: 1em;
  font-family: "Source Sans Pro", sans-serif;
  cursor: pointer;
  background-color: transparent;
  color: rgba(255, 255, 255, 0.7);
  border: 3px solid rgba(255, 255, 255, 0.2);
  margin: 0 auto;
  margin-top: 2em;
  outline: none;
}

.order-btn:hover {
  background-color: rgba(255, 255, 255, 0.7);
  color: black;
}

.successful-message {
  text-align: center;
  margin-top: 3em;
  letter-spacing: 3px;
}

@media (max-width: 600px) {
  .buyImg {
    max-width: 120px;
  }
}

.delete-fav-btn {
  display: block;
  width: 100%;
  border: none;
  cursor: pointer;
  padding: 0.5em 0;
  padding-bottom: 0.7em;
  font-size: 1em;
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
  display: flex;
  justify-content: center;
}

.delete-fav-btn:hover,
.delete-fav-btn:focus {
  background-color: rgba(206, 23, 23, 0.85);
  color: rgba(255, 255, 255, 1);
}

.no-favorite-msg {
  text-align: center;
  font-style: italic;
  letter-spacing: 1.3px;
}

